import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import {CommonModule} from '@angular/common';
import {Routes,RouterModule} from '@angular/router';

import { AppComponent } from './app.component';
import { OrderDetaileComponent } from './order-detaile/order-detaile.component';
import {PageNotFoundComponent } from './page-not-found/page-not-found.component';
import { OrderlistComponent } from './orderlist/orderlist.component';


const routes: Routes = [
  {path:'', component: OrderlistComponent},
  {path:'order_list',component:OrderlistComponent},
  {path:'order_detail', component: OrderDetaileComponent },
  {path: "**", component: PageNotFoundComponent }  // Wildcard route for a 404 page

]
@NgModule({
  declarations: [
    AppComponent,
    OrderDetaileComponent,PageNotFoundComponent, OrderlistComponent
    
  ],
  imports: [
    BrowserModule,CommonModule,RouterModule.forRoot(routes)
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }
